#root{
  height: inherit;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: #446CB3;
}
.App{
  background: red;
    text-align: center;
    padding: 5rem 0 0 0;
    width: 50vw;
    height: 50vh
}
input[type="email"] {
  width: 80%;
  padding: 1rem;
  font-size: 1rem;
  border: none;
  border-bottom: solid 2px #c9c9c9;
  -webkit-transition: border 0.3s;
  -o-transition: border 0.3s;
  transition: border 0.3s;
  text-align: center;
}

input[type="password"] {
  width: 80%;
  margin-top: 2rem;
  padding: 1rem;
  font-size: 1rem;

  border: none;
  border-bottom: solid 2px #c9c9c9;
  -webkit-transition: border 0.3s;
  -o-transition: border 0.3s;
  transition: border 0.3s;
  text-align: center;
}
input[type="email"]:focus,
input[type="password"].focus {
  border-bottom: solid 2px #969696;
}

button {
  background-color: #00B16A;
  color: #fff;

  font-size: 1rem;
  padding: 0.5rem 1rem;
  margin-top: 2rem;
  margin-right: 1rem;
}
button:hover{
  background-color: #1c875b;
}

p#red{
  color: #E26A6A;
  text-decoration: none;
}
p#green{
  color: #87D37C;
  text-decoration: none;
}

.hide{
  display: none;
}

@media (max-width: 429px) and (max-height: 430px) and (orientation:portrait){
   html{
     font-size: 10px;
   }
}
@media (max-width: 499px) and (min-height: 431px) and (max-height: 500px) and (orientation:portrait){
   html{
     font-size: 10px;
   }
   input[type="email"]{
     margin-top: 3rem;
   }
}
@media (max-width: 699px) and (min-height: 431px) and (max-height: 700px) and (orientation:portrait){
  html{
    font-size: 12px;
  }
  input[type="email"]{
